$mautim: #87509c; //Tím
$maungoc: #17c2a4; // Cyan
$mauvang: #ffdd99; // Vang
$maudo: #d74680; // Đỏ
$mauxanh: #3c5499; // Xanh
$mautrang: #fff;

@mixin home_section($bg_color,$height) {
    min-height: $height;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: $bg_color; }

@mixin section_title {
    position: relative;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 2.5rem;
    letter-spacing: 3px;
    margin-bottom: 2rem;
    &:before {
        content: '';
        display: block;
        background-color: black;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 80px;
        height: 4px;
        bottom: -20px;
        opacity: 0.1; } }

@mixin section_introduction {
    font-size: 1rem;
    font-weight: 400;
    margin-bottom: 2.6rem; }

@mixin section_img($ms) {
    max-width: $ms;
    margin-bottom: 2rem; }

@mixin circular_percentage($color, $deg, $content) {
    background: linear-gradient($deg, $color 50%, transparent 50%, transparent), linear-gradient(270deg, $color 50%, #E0E0E0 50%, #E0E0E0);
    border-radius: 50%;
    height: 150px;
    margin: 3rem auto 1.5rem;
    position: relative;
    width: 150px;
    transition: all .3s linear;
    &:before {
        background-color: #FAFAFA;
        border-radius: 50%;
        color: #4E586F;
        content: $content;
        font-size: 50px;
        font-weight: 400;
        height: 100%;
        left: 0;
        line-height: 8rem;
        position: absolute;
        text-align: center;
        top: 0;
        -webkit-transform: scale(0.87);
        transform: scale(0.85);
        width: 100%; } }

body {
    font-family: 'Titillium Web', sans-serif; }
nav {
    background: $mautim;
    min-height: 75px;
    -webkit-box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.75);
    .icon-menu {
        color: white;
        font-size: 2rem; }
    #navbarResponsive {
        li {
            a {
                color: $mautrang;
                letter-spacing: 1px;
                line-height: 1rem;
                margin-left: 0.3rem;
                width: 5rem;
                text-align: center; }
            a:hover {
                background: #401a4f;
                border-radius: 2px; } } } }
.home_slider {
    @include home_section($mautim,580px);
    h1 {
        color: #fff;
        letter-spacing: 1px;
        font-weight: 700; }
    .btn_more {
        padding: 0.9rem 3.8rem;
        background-color: #EB7D4B;
        color: $mautrang;
        text-transform: uppercase;
        margin-top: 2rem;
        border-bottom: 3px solid #C86A40;
        border-radius: 4px;
        font-size: 0.8rem;
        font-weight: bold; } }
.home_services {
    @include home_section($maungoc,580px);
    color: white;
    padding: 6rem 0;
    img {
        @include section_img(120px); }
    .services_introduction {
        @include section_introduction; }
    .services_content {
        font-size: 0.8rem;
        padding: 0.5rem; }
    h3 {
        text-transform: uppercase;
        font-size: 1rem;
        font-weight: 600;
        margin-bottom: 1.4rem; }
    h2 {
        @include section_title; } }
.home_team {
    @include home_section(#E7F1F8,580px);
    color: #3C4761;
    padding: 6rem 0;
    img {
        @include section_img(160px);
        border-radius: 50%; }
    .team_introduction {
        @include section_introduction; }
    .team_content {
        font-size: 0.8rem; }
    h3 {
        text-transform: uppercase;
        font-size: 1.3rem;
        font-weight: 700;
        color: #27283D; }
    h4 {
        font-size: 0.9rem;
        margin-bottom: 1.4rem;
        color: #66C7EB;
        font-weight: 600; }
    h2 {
       @include section_title; }
    .icon-contact {
            background: #BDD1DF;
            color: white;
            width: 2rem;
            height: 2rem;
            padding: 0.5rem;
            border-radius: 50%;
            margin-left: 8px; } }
.home_skills {
    @include home_section(white,580px);
    color: #3C4761;
    padding: 6rem 0;
    img {
        @include section_img(120px); }
    .skills_introduction {
        @include section_introduction; }
    .skills_content {
        font-size: 0.8rem; }
    h3 {
        text-transform: uppercase;
        font-size: 1.3rem;
        font-weight: 700;
        margin-bottom: 1.4rem;
        letter-spacing: 1px; }
    h2 {
        @include section_title; }
    .circular-progress-90 {
        @include circular_percentage(#30BAE7,54deg,"90%"); }
    .circular-progress-75 {
        @include circular_percentage(#D74680,0deg,"75%"); }
    .circular-progress-70 {
        @include circular_percentage(#15C7A8,-18deg,"70%"); }
    .circular-progress-85 {
        @include circular_percentage(#EB7D4B,36deg,"85%"); } }

.home_portfolio {
    @include home_section($mauvang,1280px);
    color: #3C4761;
    padding: 6rem 0;
    h2 {
        @include section_title; }
    .portfolio_introduction {
        @include section_introduction;
        padding: 0 5rem; }
    .btn-portfolio {
        background-color: #EBC985;
        color: black;
        margin-left: 10px;
        width: 4rem;
        height: auto; }
    .btn-portfolio:hover {
        background-color: #fff; }
    .btn-portfolio-container {
        display: inline-flex;
        justify-content: center;
        text-align: center; }
    h3 {
        font-size: 0.9rem;
        margin-top: 15px; }
    img {
        margin-top: 20px; }
    .btn_load {
        padding: 0.3rem 3.8rem 0;
        background-color: #17C2A4;
        color: $mautrang;
        text-transform: uppercase;
        margin: 2.5rem auto;
        border-bottom: 4px solid #118975;
        border-radius: 4px;
        font-size: 0.9rem;
        font-weight: 400;
        line-height: 45px; } }
.home_comment {
    @include home_section($maudo,800px);
    color: #3C4761;
    padding: 6rem 0;
    h2 {
        @include section_title;
        color: white; }
    .comment_introduction {
            @include section_introduction;
            color: white; }
    .comment_container {
            margin-bottom: 30px; }
    .comment_avatar {
            width: 80px;
            height: 80px;
            border-radius: 100%;
            background-color: grey;
            @media only screen and (max-width: 767px) {
                margin-left: 40%;
                width: 100px;
                height: 100px;
                margin-bottom: 15px; } }
    .comment_content {
            text-align: left;
            font-size: 0.9rem;
            .quote {
                color: white;
                font-weight: 400; }
            .name {
                color: white;
                font-weight: 600;
                margin-bottom: 0; }
            .job {
                color: #F8C795;
                font-weight: 400;
                font-size: 0.9rem;
                margin-bottom: 8px; } } }

.home_contact {
    @include home_section($mauxanh,580px);
    color: #3C4761;
    padding: 6rem 0;
    h2 {
        @include section_title;
        color: white; }
    .contact_introduction {
        @include section_introduction;
        padding: 0 5rem;
        color: white; }
    .form-container {
        justify-content: center; }
    form {
        width: 700px;
        @media only screen and (max-width: 576px) {
            width: 450px; }
        @media only screen and (max-width: 475px) {
            width: 350px; }
        input,textarea {
            background-color: #273A71;
            color: white;
            margin-top: 8px;
            border: 0; }
        input::placeholder, textarea::placeholder {
            color: white; }
        textarea {
            height: 200px; }
        .btn-submit {
            margin: 25px auto;
            padding: 0.5rem 4rem;
            background-color: #30BAE7;
            color: white;
            font-weight: 600;
            text-transform: uppercase; } } }
footer {
    background: #344B8E;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    a {
        color: #fff;
        margin: 2rem 2rem;
        font-size: 1.2rem;
        font-weight: 600;
        display: inline-block;
        @media only screen and (max-width: 1201px) {
            font-size: 0.8rem;
            margin: 1rem 2.2rem; }
        @media only screen and (max-width: 992px) {
            font-size: 0.8rem;
            margin: 1rem 1.3rem; }
        @media only screen and (max-width: 767px) {
            font-size: 0.8rem;
            margin: 1rem 0.6rem; }
        @media only screen and (max-width: 540px) {
            font-size: 0.8rem;
            margin: 1rem 0.5rem; }
        @media only screen and (max-width: 515px) {
            font-size: 0.8rem;
            margin: 1rem 0.4rem; }
        @media only screen and (max-width: 491px) {
            font-size: 0.8rem;
            margin: 1rem 0.3rem; }
        @media only screen and (max-width: 465px) {
            font-size: 0.8rem;
            margin: 1rem 0.2rem; }
        @media only screen and (max-width: 415px) {
            font-size: 0.6rem;
            margin: 1rem 0.2rem; }
        @media only screen and (max-width: 354px) {
            font-size: 0.5rem;
            margin: 1rem 0.2rem; } }
    a:hover {
        text-decoration: none;
        color: #30BAE7; } }



